<template>
    <el-tabs v-model="activename">
        <el-tab-pane label="" name="first">
            <el-form :inline="true" :model="goodsformInline" class="demo-form-inline">
                <el-form-item label="GID">
                    <el-input v-model="goodsformInline.gid" placeholder="gid"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="goodsformInline.gname" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="goodsformInline.cid2" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="goodsformInline.cid1" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="search"></el-button>
                </el-form-item>
            </el-form>
            <el-table :data="goodsArr" style="width: 100%" v-if="goodsArr.length" v-loading="loading">
                <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-form label-position="left" inline class="demo-table-expand">
                            <el-form-item label="">
                                <span>{{ props.row.cnames }}</span>
                            </el-form-item>
                            <el-form-item label="">
                                <span>{{ props.row.cname }}</span>
                            </el-form-item>
                            <el-form-item label="">
                                <span>{{ props.row.gname }}</span>
                            </el-form-item>
                            <el-form-item label="">
                                <span>{{ props.row.gdes }}</span>
                            </el-form-item>
                            <el-form-item label="">
                                <span>{{ props.row.gdiscount }}</span>
                            </el-form-item>
                            <el-form-item label="">
                                <span>{{ props.row.gprice }}</span>
                            </el-form-item>
                            <el-form-item label="">
                                <img :src="props.row.thumb" :alt="props.row.gname">
                            </el-form-item>
                            <el-form-item label="">
                                <span>{{ props.row.gtag }}</span>
                            </el-form-item>
                            <el-form-item label="">
                                <el-carousel indicator-position="outside">
                                    <el-carousel-item v-for="(item,index) in props.row.gbanner.split(',')" :key="index">
                                        <img :src="item" style="width: 100%">
                                    </el-carousel-item>
                                </el-carousel>
                            </el-form-item>
                            <el-form-item label="">
                                <span>{{ props.row.gcontent }}</span>
                            </el-form-item>
                        </el-form>
                    </template>
                </el-table-column>
                <el-table-column label="GID" prop="gid">
                </el-table-column>
                <el-table-column label="" prop="gname">
                </el-table-column>
                <el-table-column label="" prop="gdes">
                </el-table-column>
                <el-table-column
                        label="">
                    <template slot-scope="scope">
                        <router-link :to="{name:'updategoods',query:{gid:scope.row.gid,cid1:scope.row.cid1}}" tag="el-button" class="el-button el-button--text el-button--small"></router-link>
                        <el-button type="text" size="small" @click="deletegoods(scope.row.gid)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                    @size-change="sizeChange"
                    @current-change="currentChange"
                    :current-page="currentpage"
                    :page-sizes="[10,25,50,100]"
                    :page-size="size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total='total'
                    background>
            </el-pagination>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
            <el-form :model="goodsform" v-loading="loading" v-if="querylevel1cate.length" ref="goodsform" :rules="rules">
                <el-form-item label="" v-loading="loading" prop="cid1">
                    <el-select v-model="goodsform.cid1" placeholder="" @change="getlevel2cate">
                        <el-option :label="item.cname" :value="item.cid" v-for="(item,index) in querylevel1cate" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" v-loading="loading" prop="cid2">
                    <el-select v-model="goodsform.cid2" placeholder="">
                        <el-option :label="item.cname" :value="item.cid" v-for="(item,index) in querylevel2cate" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="gname" placeholder="">
                    <el-input v-model="goodsform.gname"></el-input>
                </el-form-item>
                <el-form-item label="" prop="gdes" placeholder="">
                    <el-input v-model="goodsform.gdes"></el-input>
                </el-form-item>
                <!---->
                <el-form-item label="" prop="thumb">
                    <el-input v-model="goodsform.thumb" type="hidden"></el-input>
                </el-form-item>
                <el-form-item>
                    <img :src="goodsform.thumb" alt="" width="146" v-if="goodsform.thumb">
                    <el-upload
                            action="/uploadimg"
                            list-type="picture-card"
                            :limit="1"
                            :before-upload="beforeThumbAvatarUpload"
                            :on-success="handleThumbSuccess"
                            :on-preview="handleThumbPictureCardPreview"
                            :on-remove="handleThumbRemove">
                        <i class="el-icon-plus"></i>
                        <div slot="tip" class="el-upload__tip">jpg/png/gif/webp500KB</div>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                        <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                </el-form-item>
                <el-form-item label="" prop="gtag" placeholder=",">
                    <el-input v-model="goodsform.gtag"></el-input>
                </el-form-item>
                <el-form-item label="" prop="gdiscount" placeholder="">
                    <el-input v-model.number="goodsform.gdiscount"></el-input>
                </el-form-item>
                <el-form-item label="" prop="gprice" placeholder="">
                    <el-input v-model.number="goodsform.gprice"></el-input>
                </el-form-item>
                <!---->
                <el-form-item label="" prop="gbanner">
                    <el-input v-model="goodsform.gbanner" type="hidden"></el-input>
                </el-form-item>
                <el-form-item>
                    <img :src="item" :key="index" v-for="(item,index) in bannerArr" v-if="bannerArr.length" alt="" width="146">
                    <el-upload
                            action="/uploadimg"
                            list-type="picture-card"
                            :limit="5"
                            multiple
                            :before-upload="beforeBannerAvatarUpload"
                            :on-success="handleBannerSuccess"
                            :on-preview="handleBannerPictureCardPreview"
                            :on-remove="handleBannerRemove">
                        <i class="el-icon-plus"></i>
                        <div slot="tip" class="el-upload__tip">jpg/png/gif/webp52MB</div>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                        <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                </el-form-item>
                <el-form-item label="" prop="gfee" placeholder="">
                    <el-input v-model="goodsform.gfee"></el-input>
                </el-form-item>
                <el-form-item label="" prop="gcontent" placeholder="">
                    <el-form-item>
                        <rich-texteditor @textchange="setEditor" v-model="goodsform.gcontent"></rich-texteditor>
                    </el-form-item>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" plain round @click="insertgoods('goodsform')">Submit</el-button>
                </el-form-item>
            </el-form>
        </el-tab-pane>
    </el-tabs>
</template>

<script>
    import RichTexteditor from '../common/richtexteditor/richtexteditor'
    export default {
        name: "goods",
        data() {
            return {
                goodsform: {
                    gname: '',
                    gdes: '',
                    thumb: '',
                    gtag: '',
                    gdiscount: '',
                    gprice: '',
                    gbanner: '',
                    gfee: '',
                    gcontent: '',
                    cid1: '',
                    cid2: '',
                },
                goodsArr: [],
                goodsformInline: {
                    gid: '',
                    gname: '',
                    cid1: '',
                    cid2: '',
                },
                rules: {
                    cid1: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    cid2: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    gname: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    gdes: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    thumb: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    gtag: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    gdiscount: [
                        {required: true, message: '', trigger: 'blur'},
                        {type: 'number', message: '', trigger: 'blur'}
                    ],
                    gprice: [
                        {required: true, message: '', trigger: 'blur'},
                        {type: 'number', message: '', trigger: 'blur'}
                    ],
                    gbanner: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    gfee: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    gcontent: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                },
                querylevel1cate: [],
                querylevel2cate: [],
                loading: true,
                activename: 'first',
                dialogVisible: false,
                dialogImageUrl: '',
                bannerArr: [],
                currentpage: 1,
                total: 0,
                size: 10,
            }
        },
        methods: {
            //
            getlevel1cate() {
                this.loading = true;
                this.axios.get('/admin/cate/querylevelcate').then(res => {
                    this.loading= false;
                    this.querylevel1cate = res.data;
                })
            },
            //
            getlevel2cate(val) {
                this.loading = true;
                this.axios.get('/admin/cate/querylevel2cate?cid='+val).then(res => {
                    this.loading= false;
                    this.goodsform.cid2 = '';
                    this.querylevel2cate = res.data;
                })
            },
            //
            insertgoods(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.loading = true;
                        this.axios.post('/admin/goods/insertgoods',this.goodsform).then(res => {
                            this.loading = false;
                            if (res.data.code === 0) {
                                this.$message.success(res.data.msg);
                                this.activename = 'first';
                            }
                            else if (res.data.code === 1) {
                                this.$message.error(res.data.msg)
                            }
                            else if (res.data.code === 2) {
                                this.$message.error(res.data.msg)
                            }
                        })
                    }
                });
            },
            //
            handleThumbSuccess(res,file,fileList) {
                this.goodsform.thumb = res;
            },
            //
            handleThumbRemove(file, fileList) {
                this.axios.get('/removeimg?url='+this.goodsform.thumb).then(res => {
                    if (res.data.code === 0) {
                        this.goodsform.thumb = '';
                        this.$message.success(res.data.msg);
                    }
                })
            },
            //
            handleThumbPictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            },
            //
            beforeThumbAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isPNG = file.type === 'image/png';
                const isGIF = file.type === 'image/gif';
                const isWEBP = file.type === 'image/webp';
                const isLt2M = file.size / 1024  < 500;
                if (!(isJPG || isPNG || isGIF  || isWEBP)) {
                    this.$message.error('!');
                }
                if (!isLt2M) {
                    this.$message.error(' 500KB!');
                }
                return (isJPG || isPNG || isGIF  || isWEBP) && isLt2M ;
            },
            //
            handleBannerSuccess(res,file,fileList) {
                this.bannerArr.push(res);
            },
            //
            handleBannerRemove(file, fileList) {
                this.axios.get('/removeimg?url='+file.response).then(res => {
                    if (res.data.code === 0) {
                        this.bannerArr = this.bannerArr.filter(ele => ele != file.response);
                        this.$message.success(res.data.msg);
                    }
                })
            },
            //
            handleBannerPictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            },
            //
            beforeBannerAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isPNG = file.type === 'image/png';
                const isGIF = file.type === 'image/gif';
                const isWEBP = file.type === 'image/webp';
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!(isJPG || isPNG || isGIF  || isWEBP)) {
                    this.$message.error('!');
                }
                if (!isLt2M) {
                    this.$message.error(' 2MB!');
                }
                return (isJPG || isPNG || isGIF  || isWEBP) && isLt2M ;
            },
            //
            setEditor(value) {
                this.goodsform.gcontent = value;
            },
            //
            getTables() {
                this.loading = true;
                let obj = Object.assign({},this.goodsformInline,{currentpage:this.currentpage},{size:this.size});
                this.axios.get('/admin/goods/querygoods',{params:obj}).then(res => {
                    this.loading = false;
                    this.total = res.data.total;
                    this.goodsArr = res.data.data;
                })
            },
            //
            currentChange(page) {
                this.currentpage = page;
                this.getTables();
            },
            //
            sizeChange(size) {
                this.size = size;
                this.currentpage = 1;
                this.getTables();
            },
            //
            search() {
                this.currentpage = 1;
                this.getTables();
            },
            //
            deletegoods(gid) {
                this.loading = true;
                this.axios.get('/admin/goods/deletegoods',{params:{gid:gid}}).then(res => {
                    this.loading = false;
                    if (res.data.code === 0) {
                        this.axios.get('/removeimg',{params:{url:res.data.thumb}}).then();
                        res.data.banner.forEach(v => {
                            this.axios.get('/removeimg',{params:{url:v}}).then();
                        });
                        this.goodsArr = this.goodsArr.filter(ele => ele.gid != gid);
                        --this.total;
                        this.$message.success(res.data.msg);
                    }
                    else if (res.data.code === 1) {
                        this.$message.error(res.data.msg);
                    }
                })
            },
        },
        watch: {
            bannerArr(nval) {
                this.goodsform.gbanner = nval.join();
            },
            activename(nval) {
                if (nval === 'first') {
                    this.getTables();
                }
                else if (nval === 'second') {
                    this.getlevel1cate();
                }
            }
        },
        components: {
            RichTexteditor
        },
        created() {
            this.getTables();
        },
    }
</script>

<style scoped>

    .demo-table-expand {
        font-size: 0;
        display: flex;
        flex-flow: column;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 100%;
    }

</style>
